import React, { Component } from 'react';

class Logo extends Component {
  render() {
    return (
      <a
        className="navbar__logo"
        href="https://startbootstrap.com/"
        target="_blank"
      >
        START BOOTSTRAP
      </a>
    );
  }
}

export default Logo;



